module.exports = "<h1 id=carousel>Carousel</h1> <blockquote> <p> The carousel is a slideshow for cycling through a series of content, built with CSS 3D transforms. It works with a series of images, text, or custom markup. It also includes support for previous/next controls and indicators. Please be aware that nested carousels are <strong>not</strong> supported.</p> </blockquote> <pre class=hljs><span class=hljs-tag>&lt;<span class=hljs-name>template</span>&gt;</span>\n  <span class=hljs-tag>&lt;<span class=hljs-name>div</span>&gt;</span>\n    <span class=hljs-tag>&lt;<span class=hljs-name>b-carousel</span> <span class=hljs-attr>id</span>=<span class=hljs-string>&quot;carousel1&quot;</span>\n                <span class=hljs-attr>style</span>=<span class=hljs-string>&quot;text-shadow: 1px 1px 2px #333;&quot;</span>\n                <span class=hljs-attr>controls</span>\n                <span class=hljs-attr>indicators</span>\n                <span class=hljs-attr>background</span>=<span class=hljs-string>&quot;#ababab&quot;</span>\n                <span class=hljs-attr>:interval</span>=<span class=hljs-string>&quot;4000&quot;</span>\n                <span class=hljs-attr>img-width</span>=<span class=hljs-string>&quot;1024&quot;</span>\n                <span class=hljs-attr>img-height</span>=<span class=hljs-string>&quot;480&quot;</span>\n                <span class=hljs-attr>v-model</span>=<span class=hljs-string>&quot;slide&quot;</span>\n                @<span class=hljs-attr>sliding-start</span>=<span class=hljs-string>&quot;onSlideStart&quot;</span>\n                @<span class=hljs-attr>sliding-end</span>=<span class=hljs-string>&quot;onSlideEnd&quot;</span>\n    &gt;</span>\n\n      <span class=hljs-comment>&lt;!-- Text slides with image --&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>b-carousel-slide</span> <span class=hljs-attr>caption</span>=<span class=hljs-string>&quot;First slide&quot;</span>\n                        <span class=hljs-attr>text</span>=<span class=hljs-string>&quot;Nulla vitae elit libero, a pharetra augue mollis interdum.&quot;</span>\n                        <span class=hljs-attr>img-src</span>=<span class=hljs-string>&quot;https://lorempixel.com/1024/480/technics/2/&quot;</span>\n      &gt;</span><span class=hljs-tag>&lt;/<span class=hljs-name>b-carousel-slide</span>&gt;</span>\n\n      <span class=hljs-comment>&lt;!-- Slides with custom text --&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>b-carousel-slide</span> <span class=hljs-attr>img-src</span>=<span class=hljs-string>&quot;https://lorempixel.com/1024/480/technics/4/&quot;</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>h1</span>&gt;</span>Hello world!<span class=hljs-tag>&lt;/<span class=hljs-name>h1</span>&gt;</span>\n      <span class=hljs-tag>&lt;/<span class=hljs-name>b-carousel-slide</span>&gt;</span>\n\n      <span class=hljs-comment>&lt;!-- Slides with image only --&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>b-carousel-slide</span> <span class=hljs-attr>img-src</span>=<span class=hljs-string>&quot;https://lorempixel.com/1024/480/technics/8/&quot;</span>&gt;</span>\n      <span class=hljs-tag>&lt;/<span class=hljs-name>b-carousel-slide</span>&gt;</span>\n\n      <span class=hljs-comment>&lt;!-- Slides with img slot --&gt;</span>\n      <span class=hljs-comment>&lt;!-- Note the classes .d-block and .img-fluid to prevent browser default image alignment --&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>b-carousel-slide</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>img</span> <span class=hljs-attr>slot</span>=<span class=hljs-string>&quot;img&quot;</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;d-block img-fluid w-100&quot;</span> <span class=hljs-attr>width</span>=<span class=hljs-string>&quot;1024&quot;</span> <span class=hljs-attr>height</span>=<span class=hljs-string>&quot;480&quot;</span>\n             <span class=hljs-attr>src</span>=<span class=hljs-string>&quot;https://lorempixel.com/1024/480/technics/5/&quot;</span> <span class=hljs-attr>alt</span>=<span class=hljs-string>&quot;image slot&quot;</span>&gt;</span>\n      <span class=hljs-tag>&lt;/<span class=hljs-name>b-carousel-slide</span>&gt;</span>\n\n      <span class=hljs-comment>&lt;!-- Slide with blank fluid image to maintain slide aspect ratio --&gt;</span>\n      <span class=hljs-tag>&lt;<span class=hljs-name>b-carousel-slide</span> <span class=hljs-attr>caption</span>=<span class=hljs-string>&quot;Blank Image&quot;</span> <span class=hljs-attr>img-blank</span> <span class=hljs-attr>img-alt</span>=<span class=hljs-string>&quot;Blank image&quot;</span>&gt;</span>\n        <span class=hljs-tag>&lt;<span class=hljs-name>p</span>&gt;</span>\n          Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse\n          eros felis, tincidunt a tincidunt eget, convallis vel est. Ut pellentesque\n          ut lacus vel interdum.\n        <span class=hljs-tag>&lt;/<span class=hljs-name>p</span>&gt;</span>\n      <span class=hljs-tag>&lt;/<span class=hljs-name>b-carousel-slide</span>&gt;</span>\n\n    <span class=hljs-tag>&lt;/<span class=hljs-name>b-carousel</span>&gt;</span>\n\n    <span class=hljs-tag>&lt;<span class=hljs-name>p</span> <span class=hljs-attr>class</span>=<span class=hljs-string>&quot;mt-4&quot;</span>&gt;</span>\n      Slide #: {{ slide }}<span class=hljs-tag>&lt;<span class=hljs-name>br</span>&gt;</span>\n      Sliding: {{ sliding }}\n    <span class=hljs-tag>&lt;/<span class=hljs-name>p</span>&gt;</span>\n\n  <span class=hljs-tag>&lt;/<span class=hljs-name>div</span>&gt;</span>\n<span class=hljs-tag>&lt;/<span class=hljs-name>template</span>&gt;</span>\n\n<span class=hljs-tag>&lt;<span class=hljs-name>script</span>&gt;</span><span class=javascript>\n<span class=hljs-keyword>export</span> <span class=hljs-keyword>default</span> {\n  data () {\n    <span class=hljs-keyword>return</span> {\n      <span class=hljs-attr>slide</span>: <span class=hljs-number>0</span>,\n      <span class=hljs-attr>sliding</span>: <span class=hljs-literal>null</span>\n    }\n  },\n  <span class=hljs-attr>methods</span>: {\n    onSlideStart (slide) {\n      <span class=hljs-keyword>this</span>.sliding = <span class=hljs-literal>true</span>\n    },\n    onSlideEnd (slide) {\n      <span class=hljs-keyword>this</span>.sliding = <span class=hljs-literal>false</span>\n    }\n  }\n}\n</span><span class=hljs-tag>&lt;/<span class=hljs-name>script</span>&gt;</span>\n<span class=hljs-comment>&lt;!-- carousel-1.vue --&gt;</span>\n</pre> <h2 id=sizing>Sizing</h2> <p>Carousels don&#x2019;t automatically normalize slide dimensions. As such, you may need to use additional utilities or custom styles to appropriately size content. When using images in each slide, ensure they all have the same dimensions (or aspect ratio).</p> <p>When using <code>img-src</code> or <code>img-blank</code> on <code>&lt;b-carousel-slide&gt;</code>, you can set the image width and height via the <code>img-width</code> and <code>img-height</code> props on <code>&lt;b-carousel&gt;</code> and have these values automatically applied to each <code>carousel-slide</code> image.</p> <p>Note that images will still be responsive and automatically grow or shrink to fit within the width of its parent container.</p> <p>Internally, <code>&lt;b-carousel-slide&gt;</code> uses the <a href=/docs/components/img><code>&lt;b-img&gt;</code></a> component to render the images. The <code>img-*</code> props map to the corresponsing props available to <code>&lt;b-img&gt;</code>.</p> <h2 id=interval>Interval</h2> <p>Carousel defaults to an interval of <code>5000</code>ms (5 seconds). To pause the carousel from auto sliding, set the <code>interval</code> prop to <code>0</code>. To restart a paused carousel, set the <code>interval</code> back to the number of ms.</p> <p>In browsers where the <a href=https://www.w3.org/TR/page-visibility/ >Page Visibility API</a> is supported, the carousel will avoid sliding when the webpage is not visible to the user (such as when the browser tab is inactive, the browser window is minimized, etc.).</p> <h2 id=controls-and-indicators>Controls and Indicators</h2> <p>Set the prop <code>controls</code> to enable the previous and next control buttons.</p> <p>Set the prop <code>indicators</code> to show the slide indicator buttons.</p> <p>Both indicators and controls can be set at the same time or independently.</p> <h2 id=v-model-support>V-model support</h2> <p>Programmaticaly control which slide is showing via <code>v-model</code> (which binds to the <code>value</code> prop). Note, that slides are indexed starting at <code>0</code>.</p> <h2 id=accessibility>Accessibility</h2> <p>Carousels are generally not fully compliant with accessibility standards, although we try to make them as accessible as possible.</p> <p>By providing a document unique value via the <code>id</code> prop, <code>&lt;b-carousel&gt;</code> will enable accessibility features. It is highly recommended to always add an ID to all components.</p> <p>All carousel controls and indicators have aria labels. These can be customized by setting the various <code>label-*</code> props.</p> <h2 id=component-reference>Component Reference</h2> ";

